$mobile_small: 340px;
$mobile_middle: 475px;
$mobile: 575px;

$tablet_small: 768px;
$tablet: 992px;

$desktop_extra_small: 1024px;
$desktop_small: 1140px;
$desktop_base: 1280px;
$desktop: 1366px;
$desktop_medium: 1440px;
$desktop_large: 1600px;
$desktop_huge: 1800px;

@mixin desktop_huge {
	@media screen and (max-width: $desktop_huge) {
		@content;
	}
}

@mixin min_desktop_huge {
	@media screen and (min-width: $desktop_huge + 1px) {
		@content;
	}
}

