@font-face {
	font-family: Nunito;
	font-display: swap;
	src: url("@fonts/Nunito-Bold.woff2") format("woff2"), url("@fonts/Nunito-Bold.woff") format("woff");
	font-weight: 700;
	font-style: normal;
}
@mixin FontFamilyNunitoBold {
	font-family: "Nunito", "Roboto", sans-serif;
	font-weight: 700;
	line-height: 140%;
}

@font-face {
	font-family: Nunito;
	font-display: swap;
	src: url("@fonts/Nunito-Regular.woff2") format("woff2"), url("@fonts/Nunito-Regular.woff") format("woff");
	font-weight: 400;
	font-style: normal;
}
@mixin FontFamilyNunitoRegular {
	font-family: "Nunito", "Roboto", sans-serif;
	font-weight: 400;
	line-height: 140%;
}

@font-face {
	font-family: Rubik;
	font-display: swap;
	src: url("@fonts/Rubik-Regular.woff2") format("woff2"), url("@fonts/Rubik-Regular.woff") format("woff");
